/* =============================================
  layout_header
============================================= */
.header {
  position: relative;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4vw;
  height: 60px;
}
.header-logo {
  padding: 15px;
  max-width: 260px;
}
.header-hamburger {
  z-index: 100;
}

.nav {
  position: fixed;
  z-index: 80;
  top: 0;
  right: min(-100vw, -520px);
  width: min(100vw, 520px);
  height: 100vh;
  padding-top: 20vh;
  background-color: #000;
  opacity: 0.8;
  transition: 1000ms;
}
.nav.active {
  right: 0;
}
.nav__item {
  font-size: 2rem;
  text-align: center;
}
.nav__link {
  display: block;
  padding: 20px 0;
  color: #fff;
}

.hamburger {
  z-index: 100;
  display: block;
  position: relative;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: none;
  outline: none;
  appearance: none;
  right: 40px;
}
.hamburger::after {
  position: absolute;
  content: "menu";
  color: #fff;
  right: -30px;
  top: 50%;
  transform: translateY(-50%);
}
.hamburger > span {
  display: inline-block;
  transition: all 0.4s;
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
  background: #fff;
  width: 45%;
}
.hamburger > span:nth-of-type(1) {
  top: 15px;
}
.hamburger > span:nth-of-type(2) {
  top: 23px;
}
.hamburger > span:nth-of-type(3) {
  top: 31px;
}
.hamburger.active > span:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-45deg);
  width: 30%;
}
.hamburger.active > span:nth-of-type(2) {
  opacity: 0;
}
.hamburger.active > span:nth-of-type(3) {
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(45deg);
  width: 30%;
}

